@import url(https://fonts.googleapis.com/css?family=Fira+Sans:300i,500i,700&display=swap);
* {
  color: #2b2b2b;
}

html,
body {
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Fira Sans", sans-serif;
}

a,
a:link,
a:visited,
a:active {
  color: #cb067a;
  text-decoration: none;
  font-size: 14px;
  margin-left: 25px;
}

a:hover {
  border-bottom: 1.5px solid #cb067a;
  font-style: italic;
  margin-left: 25px;
}

b {
  font-family: "Fira Sans", sans-serif;
  font-weight: 500;
  font-style: italic;
}

header {
  background-image: -webkit-gradient(linear, left top, right top, from(#2f1c94), to(#cb067a));
  background-image: linear-gradient(to right, #2f1c94, #cb067a);
  min-height: 75px;
}

h1 {
  font-size: 34.5px;
  font-family: "Fira Sans", sans-serif;
}

h3 {
  font-size: 24px;
  font-family: "Fira Sans", sans-serif;
  font-style: italic;
  font-weight: 300;
  margin-bottom: 0px;
}

li {
  list-style: none;
  margin-bottom: 12px;
  margin-left: -35px;
}

.content {
  height: 100%;
  padding: 25px 20px;
}

.inner-content {
  padding: 20px;
}

footer {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  background-image: -webkit-gradient(linear, left top, right top, from(#2f1c94), to(#cb067a));
  background-image: linear-gradient(to right, #2f1c94, #cb067a);
  min-height: 55px;
  padding: 20px 12px;
  display: flex;
  justify-content: center;
}

