@import url("https://fonts.googleapis.com/css?family=Fira+Sans:300i,500i,700&display=swap");

* {
  color: #2b2b2b;
}

html,
body {
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Fira Sans", sans-serif;
}

a,
a:link,
a:visited,
a:active {
  color: #cb067a;
  text-decoration: none;
  font-size: 14px;
  margin-left: 25px;
}

a:hover {
  border-bottom: 1.5px solid #cb067a;
  font-style: italic;
  margin-left: 25px;
}

b {
  font-family: "Fira Sans", sans-serif;
  font-weight: 500;
  font-style: italic;
}
